@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700&family=Roboto:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jua&family=Poor+Story&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700&family=Roboto:wght@400;500;700&display=swap);
/* FONTS */

/* RESET */
* {
  margin: 0;padding: 0;
  font-family: 'Noto Sans KR', 'Roboto', sans-serif; color: #2d2d2d; line-height: 1.6;
  box-sizing: border-box; outline: none;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, tbody, tfoot, thead, tr, th, td{margin:0;padding:0;}
h1, h2, h3, h4, h5, h6 {font-size: inherit;}
a {color: inherit; text-decoration: inherit;}
img {vertical-align: middle;}
a img {border: none;}
li {list-style: none;}
address, em, i {font-style: normal;}

/* COMMON */
.App{
  height: 100vh;
}

/* FLOAT & CLEARFIX */
.float--left{float: left;}
.float--right{float: right;}
.clearfix::after{content: ''; clear: both; display: block;}

/* GRID */
.container{
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(12, 1fr);
  grid-template-areas: 
  "nav nav main main main main main main main aside1 aside1 asdie1"
  "nav nav main main main main main main main aside2 aside2 asdie2";
}
#nav{grid-area: nav;}
#main{grid-area: main;}
#aside1{grid-area: aside1;}
#aside2{grid-area: aside2;}

/* CLIENTMAIN */
#clientMain{
  height: 100%;
  background: linear-gradient(19deg, rgb(237, 66, 100), rgb(95, 195, 228));  
}

/* NAV */
#nav{width: 280px; height: 100%; padding-top: 1px; background-color: rgba(2, 52, 63, .4); position: relative;}
#nav ul{margin-top: 12px;}
#nav ul li{height: 40px;}
#nav ul li a{display: block; padding-left: 12px; font-size: 18px; line-height: 40px; transition: .2s ease-in-out;}
#nav ul li a:hover{background-color: #2d2d2d; color: #fff; text-decoration: none;}

#nav .signedUser{display: -webkit-flex;display: flex; -webkit-align-items: center; align-items: center; margin-left: 12px; position: absolute; bottom: 80px;}
#nav .signedUser .signedUser--img{width: 70px; height: 70px; margin-right: 12px; border-radius: 50%;}
#nav .signedUser .signedUser--txt{cursor: pointer;}
#nav .signedUser .signedUser--txt .signedUser__email{font-size: 12px;}

#nav footer{margin-left: 12px; position: absolute; bottom: 12px; font-size: 12px;}
#nav .footer--top{letter-spacing: 1.4px;}
#nav .footer--bot{letter-spacing: 1.1px;}




.rankingContainer{height: 250px; width: 300px; overflow: auto;}
.ranking{background-color: cadetblue;}
/* AdminMain */
.AdminMain {
  background-color: #EDF5E1;
  width: 100vw;
  height: 100vh;
}

/* header */
.header {
  width: 100%;
  height: 72px;
  background-color: #5CDB95;
  font-size: 32px;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.headerText {
  padding: 4px;
  margin-left: 20vw;
  font-family: 'Jua', sans-serif;
  font-size: 48px;
}

.logout {
  margin-right: 10vw;
  cursor: pointer;
}

/* Content */
.Content {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-left: 10%;
  margin-right: 10%;
}

.ShortBox {
  background-color:white;
  padding: 15px;
  width: 340px;
  height: 520px;
  margin-top: 96px;
  box-shadow: 2px 2px 10px 1px grey;
  overflow: scroll;
  border-radius: 15px;
}

.LongBox {
  background-color:white;
  padding: 10px;
  width: 370px;
  height: 720px;
  margin-top: 96px;
  box-shadow: 2px 2px 10px 1px grey;
  overflow: scroll;
  border-radius: 15px;
}

/* MatchList */

.deleteIcon {
  position: relative;
  right: 20px;
  top: 8px;
  width: 0;
  height: 0;
}

.gameListHeader {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 12px;
  margin-top: 0;
}

/* Createuser */
.profileImg{
  width: 280px;
  height: 320px;
  margin-top: 4px;
  margin-bottom: 4px;
}

/* RegisterMatch */
.playerView {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  text-align: center;
}

.datepicker {
  margin-top: 12px;
  margin-bottom: 12px;
}

.matchResult {
  display: -webkit-flex;
  display: flex;
  text-align:center;
  margin-bottom: 12px;
}

.versus{
  margin-top: 24px;
  padding: 4px;
}

.targetUser{
  margin: 4px;
  padding-right: 8px;
  padding-left: 8px;
  padding-bottom: 2px;
  border-radius: 16px;
  font-size: 14px;
  background-color: #D1DFE8;
  display: inline-block;
}

.winnerSearchCancle {
  color: red;
  position: relative;
  right: 12px;
}

/* Auth */
.container {
  width: 100%;
  max-width: 320px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.authContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.authBtns {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 100%;
  max-width: 320px;
}

.authBtn {
  cursor: pointer;
  border-radius: 20px;
  border: none;
  padding: 10px 0px;
  font-size: 12px;
  text-align: center;
  width: 150px;
  background: white;
  cursor: pointer;
}

.authInput {
  max-width: 320px;
  width: 100%;
  padding: 10px;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 1);
  margin-bottom: 10px;
  font-size: 12px;
  color: black;
}

.authSubmit {
  text-align: center;
  background: #04aaff;
  color: white;
  margin-top: 10;
  cursor: pointer;
}

.authError {
  color: tomato;
  text-align: center;
  font-weight: 500;
  font-size: 12px;
}

.authSwitch {
  color: #04aaff;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 50px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}

/* Utility  */
.needMargin {
  margin: 4px;
}

.spaceBetween {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.displayFlex {
  display: -webkit-flex;
  display: flex;
}

